import { NextResponse } from 'next/server';

export async function GET(
  request: Request,
  context: { params: { routeid: string } }
) {
  try {
    const routeId = context.params.routeid;
    
    // Transit.land API key
    const apiKey = '4i0HhaRLe0jBSotDmxETH05X2iwrgNcJ';
    
    // Fetch data from Transit.land API
    const response = await fetch(
      `https://transit.land/api/v2/rest/routes/${routeId}?api_key=${apiKey}`,
      {
        headers: {
          'Accept': 'application/json',
        },
        next: { revalidate: 60 }, // Cache for 60 seconds
      }
    );
    
    if (!response.ok) {
      console.error(`Transit.land API error: ${response.status}`);
      return NextResponse.json({ 
        error: `Transit.land API error: ${response.status}`,
      }, { status: response.status });
    }
    
    const data = await response.json();
    
    // Return the data
    return NextResponse.json(data);
  } catch (error) {
    console.error('Error fetching route:', error);
    return NextResponse.json({ 
      error: 'Failed to fetch route',
    }, { status: 500 });
  }
} 